/* Styles go in this file. */

html, body { height: 100%; overflow: hidden; }

body {
	margin: 0;
	font-size: 12px;
	-webkit-font-smoothing: antialiased;
}

* {
	box-sizing: border-box;
}

svg {
	display: block;
}
#viz-ui {
	position: relative;
	width: 100%;
	line-height: 28px;
	button {
		background: transparent;
		color: #333;
		border-radius: 0;
		border: none;
		outline: none;
		font-family: inherit;
		font-size: 12px;
		cursor: pointer;
		height: 24px;
		line-height: 24px;
		font-weight: 700;
		padding-bottom: 0;
		margin: 0;
		background: #fff;
		&:hover {
			background: #eee;
		}
		&.selected {
			background: #2886B2;
			color: #fff;
		}
	}
	#horse-controls {
		display: inline-block;
	}
	#replay {
		border: 1px solid rgba(0,0,0,0.1);
		background: transparent;
		display: inline-block;
		vertical-align: top;
		height: 26px;
		margin-right: 8px;
		background: #fff;
		&:hover {
			background: #eee;
		}
		&:before {
			content: '';
			display: inline-block;
			background: url("./replay_icon.svg");
			width: 10px;
			height: 22px;
			margin: 0 3px 0 0;
			line-height: 24px;
			background-size: 100% auto;
			background-position: center center;
			background-repeat: no-repeat;
			vertical-align: top;
		}
	}
	#rank-toggle {
		margin-right: 8px;
		overflow: hidden;
		position: relative;
		display: inline-block;
		vertical-align: top;
		line-height: 22px;
		border: 1px solid rgba(0,0,0,0.1);
	}
	#filter-control {
		width: 300px;
		min-width: 50px;
		line-height: 22px;
		vertical-align: top;
		.dropdown, .dropdown .main {
			margin-bottom: 0px;
			max-width: 300px!important;
		}
		.dropdown .main {
			height: 24px;
			border: 1px solid rgba(0,0,0,0.1);
			font-weight: bold;
			color: black;
			* {
				line-height: 22px!important;
				font-size: 12px!important;
			}
		}
		.button {
			font-size: 12px;
			height: 21px;
			line-height: 21px;
			font-weight: 700;
			padding-bottom: 0;
			padding-top: 1px;
			border: 1px solid rgba(0,0,0,0.1);
			border-right: none;
			&:last-child {
				border-right: 1px solid rgba(0,0,0,0.1);
			}
			&.selected, &.selected:hover {
				background: #2886B2;
				color: #fff;
			}
		}
		.button-container {
			border: 0;
			line-height: 22px;
		}
	}
}


.x.axis line,
.x.axis path {
	display: none;
}

.x.axis text, .y.axis text {
	font-size: 11px;
}

.horse, .rank-number, .end.circle, .name-bg, .name-fg, image, .check line, .check circle {
	transition: 50ms opacity linear;
}

.rank-number {
	font-weight: bold;
}

.tick line {
	opacity: 1;
}

@import "customer";